.friendsWidget .media {
  display: flex;
  align-items: center; }

.friendsWidget .widgetAvatar {
  width: 2rem !important;
  height: auto !important; }

.friendsWidget .media-heading {
  font-size: 0.6rem; }

.friendsWidget .media-subheading {
  color: #003787 !important; }

.friendsWidget .media-body {
  width: 100px !important;
  max-height: 40px;
  margin: 0 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  overflow-wrap: break-word;
  white-space: break-spaces; }

.friendsWidget .media-right {
  align-items: flex-end;
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

.friendsWidget .friend-search {
  margin: 0 auto;
  border-left: 1px solid #ccd4ff; }
  .friendsWidget .friend-search input {
    border: none; }
  .friendsWidget .friend-search button {
    background-color: transparent; }

.friendsWidget .list-group .list-group-item .media-right {
  height: auto; }
