.popularArticles img {
  width: 50px;
  border-radius: 5px; }

.popularArticles .media {
  display: flex;
  align-items: center;
  justify-content: center; }

.popularArticles p {
  font-size: 14px !important;
  line-height: 16px; }

.popularArticles .thumbnail {
  box-shadow: 0 0 3px #00000099;
  border-radius: 2px;
  width: 35px;
  height: 35px;
  background-color: #555;
  background-size: cover;
  background-position: center center; }
